<template>
  <div>
    <div>
      <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('account_holder_name')}">
        <input v-validate="'required|alpha_spaces|min:3'" name="account_holder_name" id="account_holder_name" class="mdl-textfield__input" v-model="bankParams.account_holder_name">
        <label class="mdl-textfield__label" for="account_holder_name">Account Holder Name</label>
        <span class="mdl-textfield__error">{{ errors.first('account_holder_name') }}</span>
      </div>
    </div>
    <div>
      <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('account_number')}">
        <input v-validate="'required|integer|min:8'" name="account_number" id="account_number" class="mdl-textfield__input" v-model="bankParams.account_number">
        <label class="mdl-textfield__label" for="account_number">Account Number</label>
        <span class="mdl-textfield__error">{{ errors.first('account_number') }}</span>
      </div>
    </div>
    <div>
      <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('bank_name')}">
        <input v-validate="'required|alpha_spaces|min:3'" name="bank_name" id="bank_name" class="mdl-textfield__input" v-model="bankParams.name">
        <label class="mdl-textfield__label" for="bank_name">Bank Name</label>
        <span class="mdl-textfield__error">{{ errors.first('bank_name') }}</span>
      </div>
    </div>
    <div>
      <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('branch')}">
        <input v-validate="'required|alpha_spaces|min:3'" name="branch" id="branch" class="mdl-textfield__input" v-model="bankParams.branch">
        <label class="mdl-textfield__label" for="branch">Branch</label>
        <span class="mdl-textfield__error">{{ errors.first('branch') }}</span>
      </div>
    </div>
    <div>
      <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('ifsc_code')}">
        <input v-validate="'required|alpha_num|min:6'" name="ifsc_code" id="ifsc_code" class="mdl-textfield__input" v-model="bankParams.ifsc_code">
        <label class="mdl-textfield__label" for="ifsc_code">IFSC code</label>
        <span class="mdl-textfield__error">{{ errors.first('ifsc_code') }}</span>
      </div>
    </div>
    <div>
      <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('city')}">
        <input v-validate="'required|alpha_spaces|min:3'" name="city" id="city" class="mdl-textfield__input" v-model="bankParams.city">
        <label class="mdl-textfield__label" for="city">City</label>
        <span class="mdl-textfield__error">{{ errors.first('city') }}</span>
      </div>
    </div>
    <div>
      <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('state')}">
        <input v-validate="'required|alpha_spaces|min:3'" name="state" id="state" class="mdl-textfield__input" v-model="bankParams.state">
        <label class="mdl-textfield__label" for="state">State</label>
        <span class="mdl-textfield__error">{{ errors.first('state') }}</span>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'bank-form',
  props: {
    bank: {
      type: Object
    }
  },
  computed: {
    bankParams () {
      return this.bank || this.params
    }
  },
  methods: {
    valid () {
      return this.$validator.validateAll()
    },
    reset () {
      this.errors.clear()
    }
  },
  data () {
    return {
      params: {
        name: '',
        account_holder_name: this.$parent.name,
        account_number: '',
        branch: '',
        city: '',
        state: '',
        amount: 0,
        ifsc_code: ''
      }
    }
  }
}
</script>



